@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@500&display=swap);
body {overflow-x: hidden}

/* Fonts */
/* @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
body {font-family: 'Poppins', sans-serif;} */

p, .navbar, .small-nav, li {font-family: 'Nunito', sans-serif;}

h1, h2 ,h3, h4, h5, h6 {font-family: 'Playfair Display', serif;}



/* Navbar */
.small-nav {
  color: #fff;
  font-size: 11px;
}

.navbar .navbar-brand {
  color: #e26a31!important; 
  transition: all 0.3s; 
  font-weight: 700;
}

.navbar .navbar-brand:hover {color: #000 !important;}

.navbar .nav-link {transition: all 0.3s;}

.navbar .nav-link:hover {color: #e26a31!important; text-decoration: none;}

.navbar .navbar-collapse {padding-right: 50px;}


/* Home */
header {
  height: 100vh;
  background-image: url(/Restaurant--App/static/media/header.35112f97.jpg);
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
}

header .header-overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  text-align: center;
}

header .header-head {
  height: 100%;
  color: #fff;
}

header .header-head,
.events .events-head,
.book-table .book-table-body,
.about-head,
.menu-head,
.cart-head {
  width: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin: auto;
}

header .header-head p:first-child {
  margin-top: -10rem;
  text-transform: uppercase;
}

header p {
  margin: 0.5rem;
  padding: 0 4rem; 
  font-weight: 500;
}

header h1 {font-size: 75px; margin-bottom: 1rem;}

.events ul li {margin: 10px 0;}

.events .event-img {transition: all 0.5s;}

.events .event-img:hover {-webkit-transform: scale(1.1,1.1);transform: scale(1.1,1.1);}

.book-table {
  background-image: url(/Restaurant--App/static/media/book-table.b77d47e2.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-position: center center;
}

.book-table-overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(245, 201, 126, 0.75);
}

.book-table .myBtn {
  background-color: #e26a31;
  color: #fff;
  padding: 10px 20px;
  border-radius: 30px;
  border-color: #e26a31!important;
  transition: all 0.3s;
}

.book-table .myBtn:hover {
  color: #e26a31;
  background-color: #fff;
}

.main-footer .footer-item h6 {
  margin-bottom: 1.5rem;
  color: #e26a31;
}

.main-footer .footer-item li {margin-bottom: 0.5rem;}

.contact-icons i {
  margin-right: 15px;
  color: #fff;
  background-color: #e26a31;
  padding: 5px;
  border: 1px solid #e26a31;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s;
}

.contact-icons i:hover {
  color: #e26a31;
  background-color: #fff;
}

/* About */
.about-us-card {
  position: relative;
  top: 0;
  border: 0.3px solid #0000001a;
  padding: 25px;
  margin-bottom: 2rem;
  transition: all 0.3s;
}

.about-us-card:hover {
  top: -10px;
  background-color: #e26a31;
  color: #fff !important;
  cursor: pointer;
}


/* Chef */
.team-card h5 {margin-top: 1rem;}


/* Menu */
.menu .card {overflow: hidden;}

.card-img-top {height: 200px; width: 300px; transition: all 0.5s;}

.card-img-top:hover {-webkit-transform: scale(1.1,1.1);transform: scale(1.1,1.1); cursor: pointer;}

.items-container {padding: 0 1rem !important;}


/* Product */
.product,
.total {width: 40%;}

.product-cart,
.cart-btns,
.total {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center; 
  -webkit-align-items: center; 
          align-items: center;
}

.total {-webkit-justify-content: space-between;justify-content: space-between;}

.product-cart {
  margin-bottom: 15px;
  padding: 15px;
  background-color:#f1f1f1;
  border-radius: 10px;
}

.product .product-cart-img {width: 30%;}

.cart-img {
  width: 100px;
  height: 100px;
  border-radius: 10px;
}
.product .cart-btns {width: 60%;}

.product .cart-icon {
  background-color: #e26a31 !important;
  color: #fff !important;
  margin: 0 20px;
  font-weight: 700 !important;
}

.product .cart-delete {width: 10%; cursor: pointer;}


/* Shopping Cart */
.go-to-menu,
.go-to-menu:hover {
  text-decoration: none;
  color: #e26a31;
}


/* Add to Cart */
.filter .list-group-item:hover {color: #fff;}



/* Footer */
footer,
.small-nav,
.card .badge,
.filter .active,
.page-item.active .page-link,
.filter .list-group-item:hover {
  background-color: #e26a31!important;
}


.card .badge,
.product .badge,
.page-item.active .page-link {color: #fff !important;}

.navbar .active,
.page-link,
.events-head h2,
.fa-cart-plus:hover {color: #e26a31!important;}

.active, 
.page-item.active .page-link {border-color: #e26a31!important;}


/* Global classes */
.text-orange {color: #e26a31!important;}

.text-gray {color: #212529 !important;}


/* Media Query */
@media (max-width: 769px)
{

  header h1 {font-size: 55px;}

  .event-img {margin-bottom: 2rem;}

  .items-container {padding: 0 45px !important;}

  header .header-head,
  .events .events-head,
  .book-table .book-table-body,
  .about-head,
  .menu-head {
    width: 100%;
  }

  .product {width: 85%;}
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

